// src/pages/Home.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container } from '@mui/material';
import styled from '@emotion/styled';
import { playSingleAudio } from 'utils/playAudio';

import { useAtom } from 'jotai';
import { scoreAtom } from '../store';

//  
const CenteredContainer = styled(Container)`
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	text-align: center;
	cursor: pointer;
`;

const Circle = styled.div`
	background: radial-gradient(circle, wheat 0%, white 100%);
	width: 600px;
	height: 600px;
	border-radius: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	transition: background 1s, transform 1s;

	&:hover {
		background: radial-gradient(circle, lightblue 0%, white 100%);
	}

	&.animate {
		transform: scale(1.1); //     
	}
`;

const Contents = styled.div`
	font-size: 25px;
`;

function Home() {
	const navigate = useNavigate();
	const [animate, setAnimate] = useState(false); //   
	const [, setScore] = useAtom(scoreAtom);

	useEffect(() => {
		setScore(0);
	}, [setScore]);

	const handleCircleClick = () => {
		playSingleAudio('anchor.mp3');
		setAnimate(true); //   
		setTimeout(() => {
			navigate('/name-input');
		}, 1000); // 1   
	};

	return (
		<CenteredContainer maxWidth="sm" onClick={handleCircleClick}>
			<Circle className={animate ? 'animate' : ''}>
				<Contents>
					<h1>  </h1>
				</Contents>
			</Circle>
		</CenteredContainer>
	);
}

export default Home;
