let audioContext;
let gainNode;
let sourceNodes = []; //   sourceNode   

// AudioContext GainNode  
const initializeAudioContext = () => {
  if (!audioContext) {
    audioContext = new (window.AudioContext || window.webkitAudioContext)();
    gainNode = audioContext.createGain();
    gainNode.connect(audioContext.destination);
  }
};

const getAudioContext = () => {
  initializeAudioContext();
  return audioContext;
};

/**
 *        (       )
 * @param {string} fileName 
 * @param {string} [identifier]  ( )
 * @param {function} [onEndedCallback]       ( )
 */
export async function playSingleAudio(fileName, identifier, onEndedCallback) {
  //     fileName     
  if (typeof identifier !== "string") {
    onEndedCallback = identifier;
    identifier = fileName;
  }

  const audioContext = getAudioContext();
  if (audioContext.state === "suspended") {
    await audioContext.resume(); // 
  }

  try {
    const audioPath = `${process.env.PUBLIC_URL}/audio/`;
    const response = await fetch(`${audioPath}${fileName}`);
    const arrayBuffer = await response.arrayBuffer();
    const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);

    const sourceNode = audioContext.createBufferSource(); //    

    gainNode.gain.value = 1; //   

    sourceNode.buffer = audioBuffer;
    sourceNode.connect(gainNode);

    sourceNode.start(0);
    sourceNodes.push({ sourceNode, identifier }); //     

    sourceNode.onended = () => {
      sourceNodes = sourceNodes.filter((node) => node.sourceNode !== sourceNode); //     
      if (typeof onEndedCallback === "function") {
        onEndedCallback(); //   
      }
    };
  } catch (error) {
    console.error("    :", error);
  }
}

//      
export function stopAudio(identifier) {
  sourceNodes.forEach((node) => {
    if (node.identifier === identifier) {
      node.sourceNode.stop(); //    
    }
  });
  sourceNodes = sourceNodes.filter((node) => node.identifier !== identifier); //  
}

//       
export function isAudioPlaying(identifier) {
  return sourceNodes.some((node) => node.identifier === identifier);
}
