// src/pages/NameInput.js
import React, { useState } from 'react';
import { useAtom } from 'jotai';
import { Container, TextField, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { userNameAtom } from '../store';
import RankingList from '../components/RankingList';
import styled from '@emotion/styled';
import { playSingleAudio } from 'utils/playAudio';

const StyledContainer = styled(Container)`
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100vh;
`;

const StyledDiv = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	max-width: 600px;
`;

const StyledButton = styled(Button)`
	margin-top: 20px;
	width: 200px;
	font-family: jua;
	font-size: 20px;
	border-radius: 20px;
	background-color: #4caf50; /*   */
	color: white; /*    */
	&:hover {
		background-color: #45a049; /*     */
	}
`;

function NameInput() {
	const [, setName] = useAtom(userNameAtom);
	const [localName, setLocalName] = useState('');
	const [gameStartText, setGameStartText] = useState(' ');
	const [isStarting, setIsStarting] = useState(false);
	const navigate = useNavigate();

	const handleChange = (event) => {
		setLocalName(event.target.value);
	};

	const handleGameStart = () => {
		if (isStarting) return;
		setIsStarting(true);
		setName(localName);
		setGameStartText('3  ');
		playSingleAudio('countdown.wav');

		const countdown = [3, 2, 1];
		countdown.forEach((time, index) => {
			setTimeout(() => {
				setGameStartText(`${time}  `);
			}, index * 1000);
		});

		setTimeout(() => {
			navigate('/game');
		}, 3000); // 3   
	};

	return (
		<StyledContainer maxWidth="sm">
			<StyledDiv>
				<h1> </h1>
				<TextField
					label=""
					variant="outlined"
					fullWidth
					value={localName}
					onChange={handleChange}
					margin="normal"
					autoComplete="off"
				/>
				<StyledButton
					variant="contained"
					color="primary"
					fullWidth
					onClick={handleGameStart}
					disabled={isStarting} //     
				>
					{gameStartText}
				</StyledButton>

				<RankingList />
			</StyledDiv>
		</StyledContainer>
	);
}

export default NameInput;
