import React, { useState, useEffect } from 'react';
import { useAtom } from 'jotai';
import { useNavigate } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { scoreAtom, userNameAtom, rankingAtom, messageAtom } from '../store';
import { coordinates } from '../assets/coordinates';
import {
	GameContainer,
	GameTitle,
	GameInfoWrapper,
	ImageContainer,
	ImageMark,
	ProgressContainer,
	ProgressBar,
	FoundIconContainer,
	FixedMessageContainer,
} from './Game.Style.js';
import { CheckCircleOutline, CircleOutlined } from '@mui/icons-material';
import { playSingleAudio } from 'utils/playAudio';

const roundTimeLimit = 90; //     ()
const incorrectPenalty = 10; //     ()

function Game() {
	const [score, setScore] = useAtom(scoreAtom);
	const [userName] = useAtom(userNameAtom);
	const [ranking, setRanking] = useAtom(rankingAtom);
	const [message, setMessage] = useAtom(messageAtom);

	const [round, setRound] = useState(0);
	const [isGameOver, setIsGameOver] = useState(false);
	const [foundDifferences, setFoundDifferences] = useState([]);
	const [marks, setMarks] = useState([]);
	const [timeLeft, setTimeLeft] = useState(roundTimeLimit);

	const navigate = useNavigate();
	const diffCoordinates = coordinates[round];

	useEffect(() => {
		const timer = setInterval(() => {
			setTimeLeft((prevTime) => {
				if (prevTime > 0) return prevTime - 1;
				else {
					handleNext();
					return 0;
				}
			});
		}, 1000);
		return () => clearInterval(timer);
	}, [round]);

	const handleMessage = (newMessage) => {
		if (message === newMessage) {
			setMessage(' ' + newMessage + ' ');
		} else {
			setMessage(newMessage);
		}
	};

	const handleImageMouseDown = (event, diffCoordinates) => {
		if (isGameOver) return;

		const imgElement = event.target;
		const rect = imgElement.getBoundingClientRect();

		const x = event.clientX - rect.left;
		const y = event.clientY - rect.top;

		const xRatio = imgElement.naturalWidth / rect.width;
		const yRatio = imgElement.naturalHeight / rect.height;

		const actualX = x * xRatio;
		const actualY = y * yRatio;

		const clickRadius = 30;

		const isCorrect = diffCoordinates.some(
			(diff) =>
				Math.sqrt(
					Math.pow(diff.x - actualX, 2) + Math.pow(diff.y - actualY, 2)
				) < clickRadius
		);

		const mark = {
			x: (x / rect.width) * 100,
			y: (y / rect.height) * 100,
			correct: isCorrect,
			id: new Date().getTime(), //  ID 
		};

		if (isCorrect) {
			const alreadyFound = foundDifferences.some(
				(diff) =>
					Math.sqrt(
						Math.pow(diff.x - actualX, 2) + Math.pow(diff.y - actualY, 2)
					) < clickRadius
			);

			if (!alreadyFound) {
				playSingleAudio('click-success.mp3');
				// setMessage('!');
				handleMessage('!');
				setScore(score + 100); //   100
				setFoundDifferences([...foundDifferences, { x: actualX, y: actualY }]);
				setMarks([...marks, mark]);

				if (foundDifferences.length + 1 === diffCoordinates.length) {
					setTimeout(handleNext, 1000);
				}
			} else {
				handleMessage('  !');
			}
		} else {
			playSingleAudio('click-fail.mp3');
			handleMessage('!');

			if (timeLeft - 15 <= 0) {
				playSingleAudio('game-over.mp3');
				handleMessage('game over... 3   ');
				setIsGameOver(true);
				setTimeout(() => {
					navigate('/');
				}, [3000]);
				return;
			}

			setTimeLeft((prevTime) => Math.max(prevTime - incorrectPenalty, 0)); //    
			setMarks([...marks, mark]);

			setTimeout(() => {
				setMarks((prevMarks) => prevMarks.filter((m) => m.id !== mark.id));
			}, 1000); //         
		}
	};

	const handleNext = () => {
		if (round < coordinates.length - 1) {
			const newScore = score + timeLeft * 5;
			setScore(newScore);

			const msg = `${score} + ${timeLeft * 5} : ${newScore}`;
			setMessage(msg);

			setRound(round + 1);
			setFoundDifferences([]);

			setMarks([]);
			setTimeLeft(roundTimeLimit); //    
		} else {
			const newRanking = [
				...ranking,
				{
					name: userName || '',
					score,
					date: new Date().toLocaleString(),
				},
			].sort((a, b) => b.score - a.score); //   

			if (newRanking.length > 10) newRanking.splice(10); // 10 
			setRanking(newRanking);
			localStorage.setItem('ranking', JSON.stringify(newRanking));
			navigate('/ranking');
		}
	};

	const handleSlideChange = (swiper) => {
		if (swiper.activeIndex !== round) {
			swiper.slideTo(round);
		}
	};

	const ImageWithMarks = ({ src, diffCoordinates }) => (
		<ImageContainer>
			<img
				src={`${process.env.PUBLIC_URL}${src}`}
				alt=""
				width="100%"
				onMouseDown={(e) => handleImageMouseDown(e, diffCoordinates)}
				draggable="false" //  
				onDragStart={(e) => e.preventDefault()} //  
			/>
			{marks.map((mark, i) => (
				<ImageMark key={i} x={mark.x} y={mark.y} correct={mark.correct}>
					{!mark.correct && 'X'}
				</ImageMark>
			))}
		</ImageContainer>
	);

	const renderFoundIcons = () => {
		return (
			<FoundIconContainer>
				{diffCoordinates.map((_, index) =>
					foundDifferences.length > index ? (
						<CheckCircleOutline key={index} color="primary" />
					) : (
						<CircleOutlined key={index} color="disabled" />
					)
				)}
			</FoundIconContainer>
		);
	};

	const progressBarColor = () => {
		const percentage = (timeLeft / roundTimeLimit) * 100;
		if (percentage > 50) return 'green';
		else if (percentage > 25) return 'yellow';
		else return 'red';
	};

	return (
		<GameContainer>
			<ProgressContainer>
				<ProgressBar
					style={{
						height: `${(timeLeft / roundTimeLimit) * 100}%`,
						backgroundColor: progressBarColor(),
					}}>
					<div style={{ height: '100%', backgroundColor: 'transparent' }} />
				</ProgressBar>
			</ProgressContainer>
			<>
				<GameTitle>
					{round + 1}  ({round + 1}/{coordinates.length})
				</GameTitle>
				<GameInfoWrapper>
					{renderFoundIcons()}
					<p>: {score}</p>
					<p> : {timeLeft}</p>
				</GameInfoWrapper>
				<Swiper
					spaceBetween={50}
					slidesPerView={1}
					navigation={false}
					pagination={{ clickable: true }}
					scrollbar={{ draggable: true }}
					onSlideChange={handleSlideChange}
					allowTouchMove={false}>
					{coordinates.map((_, index) => (
						<SwiperSlide key={index}>
							<div style={{ display: 'flex', justifyContent: 'space-between' }}>
								<ImageWithMarks
									src={`/images/img${round + 1}1.jpg`}
									diffCoordinates={diffCoordinates}
								/>
								<ImageWithMarks
									src={`/images/img${round + 1}2.jpg`}
									diffCoordinates={diffCoordinates}
								/>
							</div>
						</SwiperSlide>
					))}
				</Swiper>
			</>
		</GameContainer>
	);
}

export default Game;
